.emails-editor_form {
  background: #FFFFFF;
  border: 1px solid #C3C2CF;
  border-radius: 4px;
  height: 80px;
  overflow-y: auto;
  padding: 8px 7px;
  cursor: text;
  overflow-wrap: break-word; }

.emails-editor_input {
  width: 2px;
  border: none;
  font-size: 14px;
  margin-right: 0; }
  .emails-editor_input:focus {
    outline: none !important; }
  .emails-editor_input::-ms-clear {
    display: none; }

.emails-editor_placeholder {
  color: #C3C2CF;
  font-size: 14px;
  line-height: 24px;
  display: inline-block; }

.emails-editor_closebtn {
  margin-left: 7px;
  color: black;
  font-weight: bold;
  float: right;
  font-size: 16px;
  line-height: 20px;
  cursor: pointer;
  transition: 0.3s; }
  .emails-editor_closebtn:hover {
    color: #C3C2CF; }

.emails-editor_email {
  font-size: 14px;
  margin-right: 8px;
  margin-bottom: 4px;
  display: inline-block;
  cursor: default; }
  .emails-editor_email__valid {
    background: rgba(102, 153, 255, 0.2);
    border-radius: 100px;
    padding: 2px 9px; }
  .emails-editor_email__invalid {
    border-bottom: 1px dashed #D92929;
    padding: 1px 0; }

.emails-editor_fake-span {
  opacity: 0;
  font-size: 14px;
  position: absolute;
  border: red; }
